<template>
  <titlepanel title="  ">

    <v-card>
    <v-card-title>
      
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label=""
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>

    <v-data-table
      :headers="headers"
      :items="products"
      :items-per-page="10"
      class="elevation-1"
      item-key="name"
      :search="search"
      :custom-filter="filterOnlyCapsText">

      <template v-slot:footer>
        <v-toolbar flat>
          <v-spacer></v-spacer>

          <v-dialog
            v-model="dialog"
            max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on">
                
              </v-btn>
            </template>

            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <div v-if="editedIndex != -1">
                  <v-row>
                    <v-text-field
                      v-model="editedItem.name"
                      label=""
                      readonly>
                    </v-text-field>
                  </v-row>
                  <v-row>
                      <v-text-field
                        class="pr-4"
                        v-model="editedItem.number"
                        label=""
                        readonly>
                      </v-text-field>
                      <v-select
                        v-model="editedItem.uint"
                        :items="productUints"
                        label=""
                        readonly>
                      </v-select>
                  </v-row>
                  </div>
                  <div v-else>
                  <v-row>
                    <v-text-field
                      v-model="editedItem.name"
                      label=""
                      :rules="[required]">
                    </v-text-field>
                  </v-row>
                  <v-row>
                      <v-text-field
                        class="pr-4"
                        v-model="editedItem.number"
                        label=""
                        :rules="[required]">
                      </v-text-field>
                      <v-select
                        v-model="editedItem.uint"
                        :items="productUints"
                        label=""
                        :rules="[required]">
                      </v-select>
                  </v-row>
                  </div>
                  <v-row>
                      <v-select
                        class="pr-4"
                        v-model="editedItem.sellUint"
                        :items="productSellUint"
                        label=""
                        :rules="[required]">
                      </v-select>
                      <v-text-field
                        v-model="editedItem.price"
                        label=""
                        :rules="[required]">
                      </v-text-field>
                  </v-row>
                </v-container>
                <v-container v-if="error" class="danger-alert">
                  {{error}}
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="close">
                  
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="save"
                  :disabled="editedItem.name=='' || editedItem.number=='' || editedItem.uint=='' || editedItem.sellUint=='' || editedItem.price==''">
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"></v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"></v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"></v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>

        </v-toolbar>
      </template>

      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)">
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)">
          mdi-delete
        </v-icon>
      </template>

      <template v-slot:no-data>
        <p class="ma-3"></p>
        <p class="ma-3"></p>
        <v-btn
          class="mb-3"
          color="primary"
          @click="initialize">
          
        </v-btn>
      </template>

    </v-data-table>
    </v-card>
  </titlepanel>
</template>

<script>
import Titlepanel from '../TitlePanel.vue'
import ProductService from '@/services/outwards/ProductService'
import SellUintService from '@/services/outwards/SellUintService'

export default {
  components: { Titlepanel },
  data: () => ({
    search:'',
    error:'',
    productUints:['', '', '', ''],
    productSellUint:[],
    required: (value) => !!value || 'Required.',
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: '',
        align: 'start',
        sortable: false,
        value: 'name',
      },
      { text: '', value: 'number', sortable: false},
      { text: '', value: 'uint', sortable: false},
      { text: '', value: 'sellUint', sortable: false},
      { text: '', value: 'price', sortable: false},
      { text: '', value: 'actions', sortable: false }
    ],
    products: [],
    editedIndex: -1,
    editedItem: {
      name: '',
      number: '',
      uint: '',
      sellUint: '',
      price: ''
    },
    defaultItem: {
      name: '',
      number: '',
      uint: '',
      sellUint: '',
      price: ''
    },
  }),

  computed: {
    formTitle () {
      return this.editedIndex === -1 ? '' : ''
    },
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },

  created () {
    this.$loading(true)
    this.initialize()
    this.$loading(false)
  },

  methods: {
    filterOnlyCapsText (value, search, item) {
      return value != null &&
            search != null &&
            typeof value === 'string' &&
            value.toString().indexOf(search) !== -1
    },

    checkInp(x){
      if (isNaN(x)){
        this.error="";
        return true;
      }
      return false;
    },
    async initialize () {
      this.products = (await ProductService.index()).data
      this.productSellUint = (await SellUintService.index()).data.map(value => value.name).sort((a,b) =>{return a.localeCompare(b, "zh-Hant")})
    },

    editItem (item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      this.editedIndex = this.products.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    async deleteItemConfirm () {
      this.$loading(true)
      try{
        await ProductService.delete(this.editedItem)
        this.closeDelete()
      }catch(err){
        console.log(err)
      }
      this.initialize()
      this.$loading(false)
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
      this.error=""
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async save () {
      this.$loading(true)
      var item = this.editedItem

      // check input is correct
      if(this.checkInp(item.number) || this.checkInp(item.price)) {
        this.$loading(false)
        return
      }
      if (this.editedIndex > -1) {
        try{
          await ProductService.put(item)
          this.initialize()
        }catch(err){
          console.log(err)
        }
      } else {
        item.full_name = `${item.name}${item.number}${item.uint}`
        try{
          await ProductService.post(item)
          this.initialize()
        }catch(err){
          console.log(err)
        }
      }
      this.close()
      this.$loading(false)
    },
  },
}
</script>
